.carousel {
  display: flex;
  width: 100%;
  height: 100%;
  align-items: center;
  &__dots{
    justify-content: center;
    bottom: 1rem;
  }
  &__dot{
    z-index: 3;
    width: 1rem;
    height: 1rem;
    border: 1px solid white;
    background: none;
    border-radius: 50%;
    transition: .3s linear;
    &:not(:last-child){
      margin-right: 2vw;
    }
  }
  .dot-active{
    background: white;
  }

  &__list {
    display: flex;
    position: relative;
    width: 100%;
    height: 100%;
    justify-content: center;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    border-radius: 12px;

