@media screen and (max-width: 1440px) {
  .footer.active{
    padding: 1.5rem 2.5rem;
  }
}
@media screen and (max-width: 1280px) {
  .filter__grid {
    gap: 8px;
  }
  .gallery__card {
    height: 45rem;
  }
  .logo{
    padding: 10px;
  }
  .p_top{
    padding-top: 86px;
  }
  .footer.active{
    padding: 1rem 2.5rem;
  }
}

@media screen and (max-width: 1024px) {
  .gallery__card {
    height: 35rem;
  }
}

@media screen and (max-width: 991px) {
  .logo-mini {
    width: 60%;
  }
  .about__grid {
    grid-template-areas:
        "nominations"
        "numbers"
        "options";
  }
  .about__grid, .about__grid-cards, .home__grid, .contacts__grid, .services__grid, .details__grid, .generation__grid-cards {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  .services-row {
    flex-direction: row;
    gap: 1rem;
    align-items: center;
  }
  .details__grid {
    grid-template-areas:
            "projects"
            "comercial"
            "music";
  }
  .contacts__grid {
    grid-template-areas:
    "photo"
    "text"
    "button"
    "photo1"
    "text1";
  }
  .main__card {
    height: 22rem;
  }
  .home {
    &__grid {
      grid-template-areas:
  "comercial"
  "music"
  "service"
  "projects"
  "about"
  "beauty"
  "contacts";
    }
  }
  .projects {
    &__grid {
      grid-template-columns: 1fr 1fr;;
    }

    &__btn {
      padding: 1.5rem;
    }
  }
  .filter__text {
    width: max-content;
    font-size: 14px;
  }
  .apps__img {
    width: 64px;
    height: 64px;
  }
  .supervisor {
    &__flex {
      gap: 1rem;
      flex-wrap: wrap;
      justify-content: center;
    }

    &__card {
      max-width: 220px;
    }
  }
  .generation__grid-links {
    gap: 1rem;
  }
  .filter__grid {
    gap: 0;
  }
  .gallery__card {
    height: 32rem;
  }
  .article {
    padding: 0 2rem;
  }
  .carousel__dot {
    width: .75rem;
    height: .75rem;
  }
}

@media screen and (max-width: 767px) {
  .form {
    &__cont {
      padding: 3rem;
    }

    &__social {
      margin-bottom: 3rem;
    }

    & > div {
      flex-direction: column;
    }
  }
  .long-btn {
    padding: 1.5rem;
  }
  .main__card {
    height: 18rem;
  }
  .about {
    &__text-cont {
      height: 100%;
      padding: 2rem;
      position: relative;
      z-index: 1;

      & p {
        padding: 1rem 0;
      }

      & h3 {
        text-align: center;
      }
    }

    &__img {
      position: absolute;
      height: auto;
      transform: translate(0, -50%);
      top: 50%;
    }
  }
  .footer > div {
    flex-direction: column;
    text-align: center;
  }
  .footer .nav {
    padding: 4rem 0;
  }
  .footer .nav, .footer .social {
    justify-content: center;
  }
  .filter__btn {
    width: 30%;
  }
  .gallery__card {
    height: 26rem;
  }
}

@media screen and (max-width: 660px) {
  .apps__img {
    width: 54px;
    height: 54px;
  }
  .long-btn {
    padding: 1.25rem;
  }
  .generation__grid-links {
    grid-template-columns: repeat(3, 1fr);
  }
  .work__card .options {
    margin-left: 1.5rem;
  }
  .gallery__card {
    height: 22rem;
  }
}

@media screen and (max-width: 550px) {
  .h1 {
    font-size: 52px;
  }
  .h2 {
    font-size: 40px;
  }
  .h3 {
    font-size: 36px;
  }
  .nav {
    justify-content: space-between;

    &__btn {
      width: 30%;
    }

    &__cont {
      padding: 1.5rem;
    }

    &__link {
      margin-right: 0 !important;
    }
  }
  .section {
    padding: 0 5vw;
  }
  .projects__grid {
    grid-template-columns: 1fr;
  }
  .contacts__text, .contacts .contacts__photo {
    padding: 1.5rem 1rem;
  }
  .contacts__text > div:last-child {
    flex-direction: column;
    gap: 1rem;
  }
  .apps__cont > div {
    flex-wrap: wrap;
    gap: 1.5rem;
    justify-content: center;
  }
  .filter__btn {
    width: 45%;
  }
  .about {
    &__img {
      position: relative;
      transform: none;
    }

    &__main-img {
      display: flex;
      flex-direction: column-reverse;
      padding-top: 2rem;
      border-radius: 25px;
    }

    &__text-cont {
      padding-top: 1rem;
    }

    & .h3 {
      font-size: 24px;
    }

    & .regular {
      font-size: 12px;
      padding: .5rem 0;
    }
  }
  .generation__grid-links {
    grid-template-columns: repeat(2, 1fr);
  }
  .gallery__card {
    height: 18rem;
  }
  .carousel__dot {
    width: .65rem;
    height: .65rem;
  }
}

@media screen and (max-width: 450px) {
  .h1 {
    font-size: 48px;
  }
  .h2 {
    font-size: 38px;
  }
  .h3 {
    font-size: 32px;
  }
  .article {
    padding: 0 1.25rem;
  }
  .apps__cont, .career__card {
    padding: 1rem 1.25rem;
  }
  .footer .nav {
    flex-direction: column;
    align-items: center;

    &__link:not(:last-child) {
      margin-bottom: 1rem;
    }
  }
  .mark {
    margin-right: 0.5rem;
  }
  .services__card {
    padding: 1.5rem 1rem;
  }
  .home {
    &__card {
      padding: 1.5rem 1rem;

      &-text-cont {
        padding: 1.5rem 1rem;
      }
    }

    &__social {
      bottom: 1.5rem;
      left: 1rem;
    }
  }
  .services {
    &__link {
      padding: 0 10px;
      text-align: center;
    }

    &-row {
      flex-direction: column;
      align-items: start;
      gap: 0;
    }
  }
  .form__cont {
    padding: 3rem 1.5rem;
  }
  .input {
    margin-bottom: 1rem;
  }
  .filter__btn {
    width: 55%;
  }
  //.carousel__item[data-pos="0"] {
  //  width: 85%;
  //}
  //.gallery__cont{
  //  padding: 1rem .25rem;
  //}
  .career__card {
    flex-direction: column;

    & .btn {
      width: 100%;
      margin-top: 1rem;
    }
  }
  .work__card .options {
    margin-left: 0;
  }
  .gallery__card {
    height: 14rem;
  }
}

@media screen and (max-width: 375px) {
  .social__cont:not(:last-child) {
    margin-right: .5rem;
  }
  .gallery__card {
    height: 12rem;
  }
}